.article{
    border: 1px solid;
    //border-radius:15px;
    //padding:30px;
    @extend %container-align;
    width:371px;
    
    img{
      width:369px;
    }
}

.f-container{
  @include flex($flex-dir: null,
                $display: flex,
                $flex-wrap: wrap,
                $align-content: normal,
                $align-items: normal,
                $justify-content:space-around,
                );
}

.intro{
  display: flex;
  background-color: $background_black;
  height: auto;
  flex-direction: row;
}

.heading{
 @include flex($flex-dir: null,
               $display: flex,
               $flex-wrap: wrap,
               $align-content: normal,
               $align-items: normal,
               $justify-content:space-between,)
}

.partners{
  @include flex($flex-dir: row,
                $display: flex,
                $flex-wrap: wrap,
                $align-content: normal,
                $align-items: normal,
                $justify-content:space-between,);
  overflow-x: hidden;
  filter: grayscale(1);
  @extend %container-align;


  img:hover{
    filter: grayscale(0);
  }
}
